/* Page header */
.page-header {
  $block: &;

  &__inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
    
    font: {
      size: rem(14px);
    };
  }

  &__holder {
    display: flex;
    align-items: center;

    &--first {
      margin-left: 0.5rem;
    }

    &--search {
      min-width: 300px;

      input {
        border-bottom: 1px black solid;
        position: relative;

        &::before,
        &::after {
          content: "";
          @include center("vertical");
          @include box(15px);
          background: {
            size: cover;
            position: center;;
          };
        }

        &::before {
          left: 0;
          background-image: url("../img/icons/raster/search.png");
        }

        &::after {
          right: 0;
          background-image: url("../img/icons/raster/list.png");
        }
      }
    }
  }

  &__button {
    position: relative;
    margin-left: 0.5rem;

    &-text {
      font: {
        weight: bold;
      };
      margin-right: 1rem;
    }

    &-icon {
      &--small {
        @include box(15px);
        @include center("vertical");
        right: 5px;
      }

      @include box(25px);
    }
  }

  &__menu {
    margin-left: 1rem;
  }
}

.page-header-menu {
  &__list {
    @include reset-list();
    @include row();
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__item {
    @include col();
  }
}