.product {
  @include col();

  &__title {
    display: block;
    white-space: pre-wrap;
  }

  &__img-wrap {
    background-color: $color-primary;
    @include box(250px);
  }

  &__price {
    width: 100%;
    display: block;
    font: {
      size: rem(30px);
    };
    display: block;
    margin: {
      top: 2rem;
      bottom: 3rem;
    }
    color: $color-secondary;
  }

  &__rating {
    display: inline-block;
  }

  &__bottom {
    flex-wrap: wrap;
    display: flex;

    form {
      margin-left: auto;
      margin-right: 0;
    }
  }
}