/* Page footer */
.page-footer {
  background-color: #212121;

  &__inner {
    padding: {
      top: 2rem;
      bottom: 20rem;
    }
    color: $color-primary;
    display: flex;
    justify-content: space-between;
  }

  &__holder {
    width: 50%;

    &:nth-child(2) {
      display: flex;
      justify-content: space-between;
    }
  }
  
  &__section {
    width: 30%;
  }

  &__list {
    @include reset-list()
  }

  &__item ~ &__item {
    margin-top: 1rem;
  }

  &__text {
    color: $color-primary;
  }

  &__icon {
    margin-right: 0.5rem;
  }
}