.item-list {
  &__list {
    @include row();
    @include reset-list();
    display: flex;
    align-items: center;
  }

  &__item {
    width: calc(100% / 6);
    @include col();
  }
}

.item-list-item {
  &__inner {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  &__img-wrap {
    background-color: $color-primary;
    border-radius: 50%;
    overflow: hidden;
    @include box(150px);
  }

  &__text {
    display: block;
    text-align: center;
    margin-top: 1rem;
    text-transform: uppercase;
    font: {
      size: rem(18px);
      weight: 300;
    }
  }
}