.news {
  &__list {
    @include row();
    @include reset-list();
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }

  &__item {
    width: 25%;
    @include col();

    &:nth-child(n+5) {
      margin-top: 2rem;
    }
  }
}

.news-item {
  $block: &;

  &__top {
    position: relative;
    overflow: hidden;

    &:hover {
      #{$block}__hover {
        transform: translateY(0);
      }
    }
  }

  &__hover {
    @include absolute($top: 0, $left: 0);
    @include box(250px);
    display: flex;
    transition: $default-transition-time transform;
    transform: translateY(100%);
    align-items: center;
    justify-content: center;
    background-color: rgba(#212121, 0.5);
  }

  &__img-wrap {
    @include box(250px);
    background-color: $color-primary;
  }

  &__title,
  &__date {
    display: block;
    font: {
      weight: bold;
      size: rem(18px);
    }
  }

  &__date {
    margin: {
      bottom: 1rem;
    }
    font: {
      size: rem(16px)
    }
  }
  
  &__subtitle {
    display: block;
    font: {
      size: rem(18px);
      weight: bold;
    };
    color: gray;
  }
}